<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a class="icon icon_manue"> manue </a>
      </div>
      <h1>flash-sales</h1>
      <div class="chartWrap" >
        <router-link :to="{path:'/search',query: {}}">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a href="javascript:void(0)" @click="goCart" class="iconfont cart"> &#xe669; </a></div>
    </section>

    <section class="tabNav">
      <div @click="tabIdx=1" :class="{'activeTab':tabIdx==1}">
        <p> 24:00</p>
        <p> On going</p>
      </div>
      <div @click="tabIdx=2" :class="{'activeTab':tabIdx==2}">
        <p> {{latestTime[0]}}</p>
        <p> 24:00 </p>
      </div>
      <div @click="tabIdx=3" :class="{'activeTab':tabIdx==3}">
        <p>{{latestTime[1]}} </p>
        <p> 24:00 </p>
      </div>
    </section>

    <section class="topTimerWrap" v-if="tabIdx==1">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Ends in</span>
        <div class="timer">
           <template v-if="remainTime[0].d!='00'">
                  <span> {{remainTime[0].d}}</span> :
            </template>
          <span> {{remainTime[0].h||"00"}} </span> : <span> {{remainTime[0].m||"00"}} </span> : <span> {{remainTime[0].s||"00"}} </span>
        </div>
      </div>
    </section>

       <section class="topTimerWrap" v-if="tabIdx==2">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Starts in</span>
        <div class="timer">
           <template v-if="remainTime[1].d!='00'">
                  <span> {{remainTime[1].d}}</span> :
            </template>
          <span> {{remainTime[1].h||"00"}} </span> : <span> {{remainTime[1].m||"00"}} </span> : <span> {{remainTime[1].s||"00"}} </span>
        </div>
      </div>
    </section>

       <section class="topTimerWrap" v-if="tabIdx==3">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Starts in</span>
        <div class="timer">
           <template v-if="remainTime[2].d!='00'">
                  <span> {{remainTime[2].d}}</span> :
            </template>
          <span> {{remainTime[2].h||"00"}} </span> : <span> {{remainTime[2].m||"00"}} </span> : <span> {{remainTime[2].s||"00"}} </span>
        </div>
      </div>
    </section>

    <section class="flashProList" v-if="tabIdx==1">
      <ul class="clearfix">
        <li  v-for="(item,index) in flashShareDay[0].productList" :key="index" >
          <div class="tips_cutOff" v-if="item.discountRate"> {{item.discountRate}} OFF </div>
          <div class="imgWrap">
            <img class="img" :src="item.spuMainImage">
          </div>
          <div class="rightMain">
            <div class="name"> {{item.name}} </div>
            <div class="priceWrap">
              <p class="red"> {{item.priceRange}} AED  </p>
              <p class="gray"> {{item.priceRangeLine}} AED </p>
            </div>
            <div class="flashDownWrap">
              <div class="bottomPrice">
                <div class="process">
                    <p class="activeBar" :style="{'width':item.soldRate}"> </p>
                    <span class="rightTips font10"> {{item.soldRate}} sold </span>
                </div>
              </div>
              <div>
                <van-button type="danger" @click="$router.push({path:'proDetail',query:{spuCode:item.spuCode}})">Shop Now</van-button>
              </div>
            </div>

          </div>

        </li>
      </ul>
    </section>

    <section class="flashProList" v-if="tabIdx==2">
      <ul class="clearfix">
        <li  v-for="(item,index) in flashShareDay[1].productList" :key="index" >
          <div class="tips_cutOff" v-if="item.discountRate"> {{item.discountRate}} OFF </div>
          <div class="imgWrap">
            <img class="img" :src="item.spuMainImage">
          </div>
          <div class="rightMain">
            <div class="name"> {{item.name}} </div>
            <div class="flashDownWrap">
              <div class="bottomPrice">
                <p class="red"> {{item.priceRange}} AED  </p>
                <p class="gray"> {{item.priceRangeLine}} AED </p>
                <div class="process">
                  <p class="activeBar" :style="{'width':item.soldRate*100}"> </p>
                  <span class="rightTips font10"> Only {{item.num}} </span>
                </div>
              </div>
              <div>
                <van-button type="danger" @click="$router.push({path:'proDetail',query:{spuCode:item.spuCode}})">See It</van-button>
              </div>
            </div>

          </div>

        </li>
      </ul>
    </section>

     <section class="flashProList" v-if="tabIdx==3">
      <ul class="clearfix">
        <li  v-for="(item,index) in flashShareDay[1].productList" :key="index" >
          <div class="tips_cutOff" v-if="item.discountRate"> {{item.discountRate}} OFF </div>
          <div class="imgWrap">
            <img class="img" :src="item.spuMainImage">
          </div>
          <div class="rightMain">
            <div class="name"> {{item.name}} </div>
            <div class="flashDownWrap">
              <div class="bottomPrice">
                <p class="red"> {{item.priceRange}} AED  </p>
                <p class="gray"> {{item.priceRangeLine}} AED </p>
                <div class="process">
                  <p class="activeBar" :style="{'width':item.soldRate*100}"> </p>
                  <span class="rightTips font10"> Only {{item.num}} </span>
                </div>
              </div>
              <div>
                <van-button type="danger" @click="$router.push({path:'proDetail',query:{spuCode:item.spuCode}})">See It</van-button>
              </div>
            </div>

          </div>


        </li>
      </ul>
    </section>

  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  name: 'flashSale',
  data () {
    return {
      pageNo:1,
      pageSize:10,
      latestTime:[],
      remainTime:[{},{},{}],
      timer:{
        "d":"00",
        "h":"00",
        "m":"00",
        "s":"00"
      },
      tabIdx:1,
      proDataBest:[
        { "id":"001",
          "cutOff":"50%",
          "img":"static/images/bestPro1.png",
          "name":"Name of the stor Name of the store and  shopeone Name of the store and  shopeone  e and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0.5
        },
        { "id":"002",
          "cutOff":"40%",
          "img":"static/images/bestPro2.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0.3
        },
        { "id":"003",
          "cutOff":"80%",
          "img":"static/images/dressPro1.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0.8
        },
        { "id":"004",
          "cutOff":"50%",
          "img":"static/images/consumerElectronicsPro1.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0.1
        }
      ],
      proDataBest2:[
        { "id":"001",
          "cutOff":"50%",
          "img":"static/images/stationeryPro1.png",
          "name":"Name of the stor Name of the store and  shopeone Name of the store and  shopeone  e and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0
        },
        { "id":"002",
          "cutOff":"40%",
          "img":"static/images/stationeryPro2.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0
        },
        { "id":"003",
          "cutOff":"80%",
          "img":"static/images/beautyCarePro1.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0
        },
        { "id":"004",
          "cutOff":"50%",
          "img":"static/images/consumerElectronicsPro2.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          'process':0
        }
      ],
    }
  },
  computed: {
    ...mapGetters(['flashShareDay','indexData']),
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
      let now=new Date();
      now.setDate(now.getDate()+1);
      this.latestTime.push(now.getMonth()+1+'/'+now.getDate());
      now.setDate(now.getDate()+1);
      this.latestTime.push(now.getMonth()+1+'/'+now.getDate());
      this.$store.dispatch('getFlashSaleDay',{pageNo:this.pageNo,pageSize:this.pageSize}).then(res=>{
        this.remainTime[0].remain=res[0].endTimeStr*1;
        this.remainTime[1].remain=res[1].beginTimeStr*1;
        this.remainTime[2].remain=res[2].beginTimeStr*1;
        this.countTime();
      });

    },
    countTime(){
      this.remainTime.forEach((element,index) => {
        let leftTime=element.remain;
        if (leftTime >= 0) {
          element.d =this.timeStr(Math.floor(leftTime  / 60 / 60 / 24)) ;
          element.h =this.timeStr(Math.floor(leftTime  / 60 / 60 % 24)) ;
          element.m =this.timeStr(Math.floor(leftTime  / 60 % 60)) ;
          element.s =this.timeStr(Math.floor(leftTime  % 60)) ;
          element.remain=element.remain-1;
          this.$set(this.remainTime,index,element);
        }
      });

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count){
      if(count<10){
        return "0"+count
      }else {
        return count
      }
    },
    goBack(){
      this.$router.back(-1)
    },
    goCart(){
      this.$router.push({
        path: "/cart",
        query: {}
      });
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/flashSale.scss";
</style>



